  },
  methods: {
    compareNames(a, b) {
      var valueA = a.value1 + a.value2;
      var valueB = b.value1 + b.value2;

      if (valueA < valueB) {
        return -1;
      }
      if (valueA > valueB) {
        return 1;
      }
      return 0;
    },
    lunbo() {
      var count = 0;
      var timeTicket = null;
      // console.log(this.$options.propsData.cdata.length);
      // var dataLength = this.$options.series[0].data.length;
      var dataLength = this.$options.propsData.cdata.length;
      // console.log(this.$refs.centreLeft2ChartRef.chart)
      var myChart = this.$refs.centreLeft2ChartRef.chart
      timeTicket && clearInterval(timeTicket);
      timeTicket = setInterval(function () {
        console.log(dataLength - (count) % dataLength-1);
        myChart.dispatchAction({
          type: 'downplay',
          seriesIndex: 0,
        });
        myChart.dispatchAction({
          type: 'highlight',
          seriesIndex: 0,
          dataIndex: dataLength - (count) % dataLength -1
        });
        myChart.dispatchAction({
          type: 'showTip',
          seriesIndex: 0,
          dataIndex: dataLength - (count) % dataLength -1
        });
        count++;
      }, 2000);

      myChart.on('mouseover', function (params) {
        console.log(params)
        clearInterval(timeTicket);
        myChart.dispatchAction({
          type: 'downplay',
          seriesIndex: 0
        });
        myChart.dispatchAction({
          type: 'highlight',
          seriesIndex: 0,
          dataIndex: params.dataIndex
        });
        myChart.dispatchAction({
          type: 'showTip',
          seriesIndex: 0,
          dataIndex: params.dataIndex,
        });
      });
      myChart.on('mouseout', function () {
        timeTicket && clearInterval(timeTicket);
        timeTicket = setInterval(function () {
          myChart.dispatchAction({
            type: 'downplay',
            seriesIndex: 0,
          });
          myChart.dispatchAction({
            type: 'highlight',
            seriesIndex: 0,
            dataIndex: dataLength - (count) % dataLength -1
          });
          myChart.dispatchAction({
            type: 'showTip',
            seriesIndex: 0,
            dataIndex: dataLength - (count) % dataLength -1
          });
          count++;
        }, 2000);
      });
    }
